.experience {
	position: relative;
	width: 100%;
	height: rem(600);
	background: url(../img/section_bg.jpg) no-repeat top center;
	background-size: cover;
	margin-bottom: rem(100);

	display: flex;
	align-items: center;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0,0,0,.75);
	}

	.container-min {
		position: relative;
	}

	&_item-top,
	&_item-bottom {
		position: absolute;
		overflow: hidden;
		z-index: 1;
		img {
			border-radius: 50%;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&_right {
		position: relative;
	}

	&_item-top {
		top: rem(25);
		right: rem(290);
		width: rem(80);
		height: rem(80);
		border-radius: 50%;
		border: rem(5) solid white;

		
	}

	&_item-bottom {
		bottom: rem(-25);
		right: rem(-40);
		width: rem(120);
		height: rem(120);
		border-radius: 50%;
		border: rem(8) solid white;
	}

	&_left {
		position: relative;
		// max-width: rem(840);
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		padding-top: rem(100);

		&::after {
			content: "";
			position: absolute;
			top: rem(92);
			right: rem(130);
			width: rem(132);
			height: rem(172);
			background: url(../img/arrow_yellow.png) no-repeat;
			background-size: 100%;
			z-index: 1;
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(-10);
			left: rem(370);
			width: rem(29);
			height: rem(29);
			background: url(../img/triangle2.png) no-repeat;
			background-size: 100%;
		}

		.listivo_button {
			width: rem(272);
		}
	}

	.listivo_title {
		position: relative;
		font-size: rem(56);
		line-height: rem(64);
		color: white;
		font-weight: 700;
		font-family: 'Comfortaa';
		// letter-spacing: rem(1.25);
		margin-bottom: rem(58);

		@include tablet {
			font-size: rem(52);
		}

		&::before {
			content: "";
			position: absolute;
			top: rem(-74);
			left: 0;
			width: rem(337);
			height: rem(55);

			background: url(../img/find_everything.png) no-repeat;
			background-size: 100%;
			z-index: 1;

		}

		&::after {
			content: "";
			position: absolute;
			top: rem(54);
			left: rem(192);
			width: rem(364);
			height: rem(9);
			background: url(../img/line2.png) no-repeat;
			background-size: 100%;
			// z-index: 0;

			@include tablet {
				// left: rem(240);
			}
		}
	}
}