:root {
	--red: #da1c2e;
	--maroon: #b81726;
	--text-color: #222222;
	--text-descr: #444444;
	--color2: #0b4355;
	--color2_1: #245666;
	--color2_2: #0b4355;
	--color3: #edb509;
	--color3_1: #ffa800;
	--color4: #ffe9eb;
	--color5: #e7ffe6;
	--color5_1: #fff9db;
	--color5_2: #f7eeff;
	--color6: #b15dff;
	--font-brittany: 'Brittany';
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;

	@include tablet {
		font-size: 87%;
	}
	
	@include hd {
		font-size: 180%;
	}
	@include w1200_w900 {
		font-size: 75%;
	}

	@include min320 {
		font-size: 90%;
	}
	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

body {
	position: relative;
	font-family: 'Inter';
	font-weight: 400;
	font-size: rem(16);
	color: var(--text-color);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.relative {
	position: relative;
}
.container-fluid {
	position: relative;
	width: 100%;
	margin: 0 auto;
}
.container-max {
	width: 1650px;
	margin: 0 auto;

	@include tablet {
		width: 1320px;
	}
}
.container-min {
	width: 1140px;
	margin: 0 auto;
}
.container {
	width: 1240px;
	margin: 0 auto;

	// @include max {
	// 	width: 1300px;
	// }

	@include w1200_w900 {
		width: 900px;
	}

	@include min {
		width: calc(100% - 40px);
	}

	@include max {
		width: 1600px;
	}

	@include hd {
		width: 2400px;
	}
	@include w1200_w900 {
		width: 900px;
	}
}

.grid-col-right {
	display: grid;
	grid-template-columns: calc((100% - 1240px)/2) 4.3fr 5.7fr calc((100% - 1240px)/2);

	@include max {
		grid-template-columns: calc((100% - 1600px)/2) 4.3fr 5.7fr calc((100% - 1600px)/2);
	}

	@include hd {
		grid-template-columns: calc((100% - 2400px)/2) 4.3fr 5.7fr calc((100% - 2400px)/2);
	}

	@include w1200_w900 {
		grid-template-columns: calc((100% - 900px)/2) 4.3fr 5.7fr calc((100% - 900px)/2);
	}

	@include min {
		grid-template-columns: calc((100% - 900px)/2) 5fr 5fr calc((100% - 900px)/2);
	}

	
}


.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}


.listivo_button {
	position: relative;
	background-color: var(--red);
	border-radius: rem(8);
	text-transform: uppercase;
	color: white;
	font-weight: 600;
	height: rem(56);
	padding: 0 rem(80) 0 rem(25);
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;

	.listivo_icon {
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		color: white;
		background-color: var(--maroon);
		width: rem(68);
		clip-path: polygon(30% 0, 100% 0, 100% 100%, 0% 100%);
	}
}

.listivo_button_lite {
	position: relative;
	background-color: #f2fafc;
	font-size: rem(14);
	border-radius: rem(8);
	// font-weight: 600;
	height: rem(50);
	padding: 0 rem(80) 0 rem(25);
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;

	.listivo_icon {
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		color: #84b3c2;
		background-color: #e6f4f8;
		width: rem(45);
		clip-path: polygon(30% 0, 100% 0, 100% 100%, 0% 100%);
	}
}

.object-cover {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.title_block {
	display: flex;
	flex-direction: column;
	align-items: center;

	&-top {
		margin-bottom: rem(16);
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&::before {
			content: "";
			width: rem(25);
			height: rem(1);
			background: url(../img/title_line.png) no-repeat;
			background-size: 100%;
			margin-right: rem(14);
		}

		&::after {
			content: "";
			width: rem(25);
			height: rem(1);
			background: url(../img/title_line.png) no-repeat;
			background-size: 100%;
			margin-left: rem(14);
			transform: rotate(180deg);
		}

		.listivo_text {
			font-size: rem(18);
			font-weight: 300;
		}
	}
	&-bottom {
		position: relative;

		.listivo_text {
			font-size: rem(54);
			font-weight: 700;
			font-family: 'Comfortaa';
			text-align: center;
		}
	}

}


.hidden {
	display: none;
}

.listivo_button_white {
	padding: rem(14) rem(28);
	background-color: transparent;
	border-radius: rem(8);
	font-weight: 500;
	transition: all 300ms ease;

	&:hover {
		background-color: var(--red);
		color: white;
	}
	
}
.listivo_button_white.active {
	background-color: var(--red);
	color: white;
}

.listivo_button_r-icon {
	border-radius: 50%;
	width: rem(40);
	height: rem(40);
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: var(--color6);

	i {
		color: white;
	}
}